:root {
  --color-primary: #33c3f0;
  --color-primary-darken-20: #10a9d9;
  --color-secondary: #f0f0f0;
  --color-secondary-darken-20: silver;
  --color-black: black;
  --color-black-at-alpha-05: rgba(0, 0, 0, 0.05);
  --color-white: white;
  --color-font: #3e3e3e;
  --color-border: #dadada;
}

html {
  font-size: 62.5%;
  line-height: 1.6;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

body {
  color: var(--color-font);
  font-weight: normal;
  font-size: 15px;
  font-family: "Roboto", sans-serif;
  font-style: normal;
}

h1 {
  margin: 0 0 1.5rem 0;
  color: var(--color-font);
  font-weight: 300;
  font-size: 4rem;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
}

h2 {
  margin: 0 0 1.5rem 0;
  color: var(--color-font);
  font-weight: 300;
  font-size: 3.4rem;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
}

h3 {
  margin: 0 0 1.5rem 0;
  color: var(--color-font);
  font-weight: 300;
  font-size: 2.8rem;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
}

p {
  margin-bottom: 1.5rem;
}

strong {
  font-weight: 700;
}

small {
  font-size: 80%;
}

ul {
  margin: 0 0 1.5rem 0;
  padding: 0;
  list-style: circle inside;
}
ul ul,
ul ol {
  margin: 0 0 0 2rem;
}

ol {
  margin: 0 0 1.5rem 0;
  padding: 0;
  list-style: decimal inside;
}
ol ul,
ol ol {
  margin: 0 0 0 2rem;
}

hr {
  box-sizing: content-box;
  height: 0;
  margin: 1.5rem 0;
  overflow: visible;
  border: 0;
  border-top: 1px solid var(--color-border);
}

blockquote {
  margin: 0 0 1.5rem 0;
  padding: 1rem 1.5rem;
  border-left: 3px solid var(--color-border);
}

a {
  color: var(--color-primary);
  text-decoration: none;
  background-color: transparent;
}

code {
  padding: 0.2rem 0.5rem;
  color: var(--color-font);
  font-size: 100%;
  font-family: "Inconsolata", monospace;
  word-break: break-word;
  background: rgba(0, 0, 0, 0.05);
  border: 1px solid var(--color-border);
  border-radius: 0.3rem;
}

pre {
  display: block;
  margin: 0 0 1.5rem 0;
  padding: 1rem 1.5rem;
  overflow-x: auto;
  color: var(--color-font);
  font-size: 95%;
  font-family: "Inconsolata", monospace;
  white-space: pre;
  background: rgba(0, 0, 0, 0.05);
  border: 1pxsolid var(--color-border);
  border-radius: 0.3rem;
}

table {
  width: 100%;
  max-width: 100%;
  margin: 0 0 1.5rem 0;
  font-size: 90%;
  background: transparent;
  border-collapse: collapse;
  border-spacing: 0;
}
table caption {
  padding: 1rem;
}
table th {
  padding: 1rem;
  text-align: left;
  vertical-align: top;
}
table td {
  padding: 1rem;
  text-align: left;
  vertical-align: top;
  border-top: 1px solid var(--color-border);
}
table.table-bordered {
  border: 1px solid var(--color-border);
}
table.table-striped > tbody > tr:nth-child(odd) {
  background: var(--color-black-at-alpha-05);
}

form {
  margin-bottom: 1.5rem;
}

label {
  display: block;
}

.field {
  box-sizing: border-box;
  padding: 0.8rem 1rem;
  color: var(--color-font);
  font-weight: normal;
  font-size: 1.5rem;
  font-family: inherit;
  font-style: normal;
  line-height: 1.6;
  background: transparent;
  border: 1px solid var(--color-border);
  border-radius: 0.3rem;
  box-shadow: none;
}
select.field {
  text-transform: none;
}
select.field:not([multiple]) {
  height: calc((0.8rem * 2) + (1.6 * 1.5rem) + (1px * 2));
}

textarea.field {
  overflow: auto;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.button {
  display: inline-block;
  box-sizing: border-box;
  padding: 0.8rem 1rem;
  overflow: visible;
  font-size: 1.5rem;
  font-family: inherit;
  line-height: 1.6;
  white-space: nowrap;
  text-align: center;
  text-decoration: none;
  border-radius: 0.3rem;
  cursor: pointer;
  -webkit-appearance: button;
  -moz-appearance: button;
  appearance: button;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
